    url('../fonts/Gotham-Medium/GothamPro-Medium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    }
@font-face{
    font-family: 'Gotham';
    src: url('../fonts/Gotham/GothamPro.eot') format('eot'),
    url('../fonts/Gotham/GothamPro.woff') format('woff'),
    url('../fonts/Gotham/GothamPro.ttf') format('truetype');
