$main-color: #f5f5f5;
$color-blue:#11749e;
$color-blue-light:#2e85aa;
$dark-color: #222222;
$light-color: #f3f3f3;

h1, h2, h3, h4, p {
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Gotham Medium';
}

.wrap {
    max-width: 1170px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    &-news {
        max-width: 100%;
        padding: 0;
    }
}

.page {
    position: relative;
    padding-top: 100px;
    padding-bottom: 126px;
    &__title {
        position: absolute;
        font-size: 37.55px;
        font-family: 'Gotham';
        text-transform: uppercase;
        margin: 67px auto 0 auto;
        left: 0;
        right: 0;
        span {
            display: block;
            font-family: 'Gotham book';
            font-size: 30.06px;
            color: $dark-color;
            text-transform: none;
            margin-top: 100px;
        }
    }
    &__img {
        opacity: 0.7;
    }
    &-main {
        background-image: url(../img/Background_header.png);
        background-repeat: no-repeat;
        background-size: cover;
        padding-bottom: 196px;
        &::before {
            content: '';
            width: 100%;
            height: 1px;
            background-color: $main-color;
            display: block;
            position: absolute;
            top: 114px;
        }
    }
    &-blue {
        background-color: $color-blue;
        text-align: center;
        &__title {
            position: absolute;
            font-size: 37.55px;
            font-family: 'Gotham';
            color: $main-color;
            text-transform: uppercase;
            margin: 67px auto 0 auto;
            left: 0;
            right: 0;
        }
        &__img {
            opacity: 0.7;
        }
        &-small {
            background-color: $color-blue;
            text-align: start;
            padding-bottom: 153px;
        }
    }
    &-news {
        padding-top: 0;
        padding-bottom: 0;
    }
    &-blog {
        padding-top: 117px;
        padding-bottom: 107px;
        text-align: center;
    }
    &-features {
        background-image: url(../img/BG_features.png);
        background-repeat: no-repeat;
        background-size: cover;
        padding-bottom: 94px;
    }
    &-team {
        padding-top: 66px;
        padding-bottom: 125px;
        text-align: center;
        span {
            margin-top: 128px;
        }
    }
    &-contact {
        padding-top: 145px;
        padding-bottom: 102px;
        text-align: center;
        background-image: url(../img/bg_contact.png);
        background-repeat: no-repeat;
        background-size: cover;
        &__img {
            opacity: 1;
        }
        &__title {
            color: #ffffff;
        }
    }
    &-footer {
        background-color: $dark-color;
        padding-top: 53px;
        padding-bottom: 50px;
    }
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1170px;
    margin: 0 auto;
    &__logo {
        display: block;
        width: 79px;
        height: 75px;
        background-image: url(../img/svg/Logo.svg);
        margin: 21px 0px;
    }
    &-list {
        display: flex;
        justify-content: space-evenly;
        list-style-type: none;
        font-family: 'MyriadPro';
        font-size: 20px;
        align-items: center;
        color: $light-color;
        margin-top: 15px;
        margin-left: 194px;
        flex-basis: 62.5%;
        line-height: 93px;
        &__item {
            margin-left: 22px;
            // padding-bottom: 5px;
            border-bottom: 5px solid transparent;
            transition: 0.3s;
            &:hover {
                border-bottom: 5px solid $color-blue;
                z-index: 1;
            }
        }
        &__item.active {
            border-bottom: 5px solid $color-blue;
            z-index: 1;
        }
        &__link {
            color: $light-color;
            text-decoration: none;
            letter-spacing: 0.5px;
        }
    }
    &__social {
        display: flex;
        list-style-type: none;
        margin-top: 13px;
        &>li {
            margin-left: 27px;
            &>a{
                color: $main-color;
            }
        }
    }
}

.block-agency {
    &-item {
        padding-top: 112px;
        p {
            color: white;
            font-family: 'Open Sans', sans-serif;
            font-size: 13px;
            text-align: center;
            vertical-align: middle;
        }
        &__scroll {
            position: absolute;
            top: 706px;
            left: 50%;
            width: 64px;
            height: 64px;
            margin-left: -34px;
            border: 1px solid #fff;
            border-radius: 100%;
            opacity: 0;
            animation: sdb03 3s infinite;
            box-sizing: border-box;
            &::after {
                position: absolute;
                top: 50%;
                left: 50%;
                content: '';
                width: 14px;
                height: 14px;
                margin: -11px 0 0 -6px;
                border-left: 5px solid #fff;
                border-bottom: 5px solid #fff;
                transform: rotate(-45deg);
                box-sizing: border-box;
            }
            // &::before {
            //     position: absolute;
            //     top: 0;
            //     left: 0;
            //     z-index: -1;
            //     content: '';
            //     width: 64px;
            //     height: 64px;
            //     box-shadow: 0 0 0 0 rgba(255,255,255,.1);
            //     border-radius: 100%;
            //     animation: sdb03 3s infinite;
            //     box-sizing: border-box;
            // }
            @keyframes sdb03 {
                0% {
                  opacity: 0;
                }
                30% {
                  opacity: 1;
                }
                60% {
                  box-shadow: 0 0 0 60px rgba(255,255,255,.1);
                  opacity: 0;
                }
                100% {
                  opacity: 0;
                }
            }
        }  
    }        
}

.block-text {
    display: flex;
    justify-content: center;
    &__title {
        font-family: 'Gotham Bold';
        text-transform: uppercase;
        font-size: 153.54px;
        letter-spacing: -7.2px;
        color: $main-color;
        margin-top: 237px;
        span {
            display: block;
            font-family: 'Gotham';
            text-align: center;
            font-size: 24.79px;
            letter-spacing: 17px;
            color: $color-blue;
        }
    }
}

.block-about {
    display: flex;
    justify-content: space-around;
    margin-top: 67px;
    &__btn {
        width: 133px;
        height: 133px;
        border: none;
        background-color: $color-blue-light;
        border-radius: 100%;
        line-height: 0;
        font-size: 0;
        cursor: pointer;
        margin-bottom: 19px;
        &:focus {
            outline: none;
        }

    }
    &-item {
        text-align: center;
        color: $main-color;
        h4 {
            text-transform: uppercase;
            font-family: 'Gotham';
            font-size: 18px;
            margin-bottom: 24px;
        }
        p {
            font-family: 'Gotham Book';
            font-size: 15px;
            letter-spacing: 0.4px;
            line-height: 24px;
        }
    }
}
.block-news {
    &-item {
        display: flex;
        height: 447px;
        &-info1 {
            flex-direction: column;
            padding-left: 350px;
            padding-top: 76px;
            background-color: $dark-color;
            width: 63%;
            color: #fff;
            &__tag {
                display: inline-flex;
                font-family: 'Gotham book';
                font-size: 13px;
                margin-bottom: 13px;
                span {
                    margin: auto 12px;
                }
            }
            &__title {
                font-family: 'Gotham Medium';
                font-size: 25px;
                text-transform: uppercase;
                margin-bottom: 32px;
            }
            &__text {
                font-family: 'Gotham book';
                font-size: 15px;
                line-height: 1.6;
                max-width: 476px;
            }
        }
        &-img1 {
            background-image: url(../img/img_news1.png);
            background-repeat: no-repeat;
            background-size: cover;
            width: 100%;
        }
        &-info2 {
            flex-direction: column;
            background-color: $light-color;
            padding-left: 80px;
            padding-top: 99px;
            width: 91%;
            &__tag {
                display: inline-flex;
                font-family: 'Gotham book';
                font-size: 13px;
                margin-bottom: 13px;
                span {
                    margin: auto 12px;
                }
            }
            &__title {
                font-family: 'Gotham Medium';
                font-size: 25px;
                text-transform: uppercase;
                margin-bottom: 32px;
            }
            &__text {
                font-family: 'Gotham book';
                font-size: 15px;
                line-height: 1.6;
                max-width: 476px;
            }
        }
        &-img2 {
            background-image: url(../img/img_news2.png);
            background-repeat: no-repeat;
            background-size: cover;
            width: 100%;
        }
    }
}
.block-blog {
    display: flex;
    justify-content: space-between;
    margin-top: 134px;
    h4 {
        font-family: 'Gotham';
        font-size: 18px;
        color: $color-blue;
        text-transform: uppercase;
        margin-top: 37px;
    }
    &__desc {
        font-size: 15px;
        font-family: 'Gotham Book';
        margin-top: 16px;
        line-height: 1.6;
        max-width: 267px;
    }
    &-img {
        width: 100%;
        height: 290px;
        overflow: hidden;
        position: relative;
        img {
            width: 100%;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }
}
.block-quick {
    display: flex;
    justify-content: space-between;
    margin-top: 6px;
    &-item {
        display: flex;
        max-width: 555px;
        &__img {
            display: block;
            margin-top: 8px;
        }
        p {
            flex-basis: 82%;
            font-family: 'Gotham Book';
            font-size: 15px;
            color: #ffffff;
            line-height: 1.6;
            margin-left: 34px;
        }
    }
}
.block-features {
    display: flex;
    // justify-content: space-around;
    &-text {
        margin-top: 123px;
        margin-left: 9px;
        flex-basis: 45%;
