        }
        p {
            font-family: 'Gotham Book';
            font-size: 15px;
            color: $light-color;
            // max-width: 61%;
            line-height: 1.6;
            margin-bottom: 65px;
        }
        &-btn {
            display: flex;
            flex-direction: column;
            &__btn1 {
                border: none;
                background-color: $color-blue;
                text-align: left;
                font-family: "Gotham medium";
                font-size: 15px;
                color: $light-color;
                text-transform: uppercase;
                max-width: 70%;
                padding: 10px;
                margin-bottom: 25px;
                &:focus {
                    outline: none;
                }
            }
            &__btn2 {
                border: none;
                background-color: $color-blue;
                text-align: left;
                font-family: "Gotham medium";
                font-size: 15px;
                color: $light-color;
                text-transform: uppercase;
                max-width: 75%;
                padding: 10px;
                margin-bottom: 25px;
                &:focus {
                    outline: none;
                }
            }
            &__btn3 {
                border: none;
                background-color: $color-blue;
                text-align: left;
                font-family: "Gotham medium";
                font-size: 15px;
                color: $light-color;
                text-transform: uppercase;
                max-width: 80%;
                padding: 10px;
                margin-bottom: 25px;
                &:focus {
                    outline: none;
