            }

        }
    }
    &-img {
        margin-left: 162px;
    }
    
}
.block-team {
    display: flex;
    justify-content: space-between;
    &-item {
        position: relative;
        margin-top: 161px;
        &-img {
            width: 270px;
            height: 414px;
            overflow: hidden;
            z-index: -1;
            position: relative;
            img {
                width: 270px;
                height: 414px;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
                z-index: -1;
            }
        }
        &-text {
            position: absolute;
            background-color: #ffffff;
            padding: 10px 40px;
            right: 24px;
            bottom: -30px;
            h5 {
                font-family: "Gotham Medium";
                font-size: 20px;
                text-transform: uppercase;
                color: $color-blue;
                line-height: 2;
            }
            p {
                font-family: "Gotham Medium";
                font-size: 15px;
            }
        }
    }
}
.block-contact {
    &-input {
        display: flex;
        justify-content: space-between;
        margin-top: 70px;
        fieldset {
            border: 1px solid #ffffff;
            padding: 9px 11px 7px 17px;
            input {
                background: none;
                border: none;
                width: 340px;
                height: 28px;
                color: #ffffff;
                font-family: "Gotham book";
                font-size: 15px;
                &:focus {
                    outline:none;
                }
                &::placeholder {
                    color: #ffffff;
                    font-family: "Gotham book";
                    font-size: 15px;
                }
            }
        }
        
    }
    &-message {
        margin-top: 39px;
        fieldset {
            border: 1px solid #ffffff;
            padding: 12px 12px 12px 16px;

            textarea {
                width: 100%;
                background: none;
                border: none;
                color: #ffffff;
                font-family: "Gotham book";
                font-size: 15px;
                &:focus {
                    outline:none;
                }
                &::placeholder {
                    color: #ffffff;
                    font-family: "Gotham book";
                    font-size: 15px;
                }
            }
        }
    }
    &__btn {
        background: $color-blue;
        border: none;
        font-family: "Gotham Medium";
        font-size: 24px;
        color: #ffffff;
        text-transform: uppercase;
        margin-top: 45px;
        margin-left: 84px;
        padding: 18px 66px;
        &:focus {
            outline: none;
        }
    }
}
#map {
    height: 333px;
    width: 100%;
}
.block-footer {
    p {
        font-family: 'Roboto', sans-serif;
        font-size: 13px;
        color: $light-color;
        letter-spacing: 0.4px;
    }
}